}\n\n.cc-highlight .cc-btn:first-child {\n  background-color: transparent;\n  border-color: transparent;\n}\n\n.cc-highlight .cc-btn:first-child:hover,\n.cc-highlight .cc-btn:first-child:focus {\n  background-color: transparent;\n  text-decoration: underline;\n}\n\n.cc-close {\n  display: block;\n  font-size: 1.6em;\n  opacity: 0.9;\n  width: 100%;\n  height: 100%;\n  /* seeing as this contains text and not an image, the element taller than it is wide (because it is text) */\n  /*  - we want it to be a square, because it's acting as an icon */\n  /*  - setting the line height normalizes the height */\n  line-height: 0.75;\n}\n\n.cc-close:hover,\n.cc-close:focus {\n  opacity: 1;\n}\n\n/* This file should contain styles that modifies the popup layout. */\n/* By layout, we mean the physical position of the elements on the popup window, and the margin / padding around those elements. */\n/**************************************** FLOATING ****************************************/\n/* these classes position the floating element */\n.cc-top {\n  top: 1em;\n}\n\n.cc-left {\n  left: 1em;\n}\n\n.cc-right {\n  right: 1em;\n}\n\n.cc-bottom {\n  bottom: 1em;\n}\n\n/* links that are direct decendants should be displayed as block */\n.cc-floating > .cc-link {\n  margin-bottom: 1em;\n}\n\n.cc-floating .cc-message {\n  display: block;\n  margin-bottom: 1em;\n}\n\n.cc-window.cc-floating .cc-compliance {\n  flex: 1 0 auto;\n}\n\n/**************************************** BANNER ****************************************/\n.cc-window.cc-banner {\n  align-items: center;\n}\n\n.cc-banner.cc-top {\n  left: 0;\n  right: 0;\n  top: 0;\n}\n\n.cc-banner.cc-bottom {\n  left: 0;\n  right: 0;\n  bottom: 0;\n}\n\n.cc-banner .cc-message {\n  display: block;\n  flex: 1 1 auto;\n  max-width: 100%;\n  margin-right: 1em;\n}\n\n/* COMPLIANCE BOX */\n.cc-compliance {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n}\n\n.cc-floating .cc-compliance > .cc-btn {\n  flex: 1;\n}\n\n.cc-btn + .cc-btn {\n  margin-left: 0.5em;\n}\n\n/* Categories Layout */\n.cc-window.cc-type-categories {\n  display: inline-flex;\n  flex-direction: column;\n  overflow: visible;\n}\n.cc-window.cc-type-categories .cc-btn {\n  margin: 0;\n}\n.cc-window.cc-type-categories .cc-btn.cc-save {\n  margin: 0;\n  display: inline-block;\n}\n\n.cc-categories {\n  display: flex;\n  flex-direction: column;\n}\n.cc-categories .cc-category {\n  display: flex;\n  max-width: 100%;\n  position: relative;\n}\n.cc-categories .cc-btn {\n  border-right: none;\n  outline: none;\n  text-transform: capitalize;\n}\n.cc-categories .cc-info {\n  border-left: none;\n  border-right: 2px solid lightgrey;\n  padding: 4px;\n  font-variant: all-small-caps;\n}\n.cc-categories .cc-info:focus + .cc-tooltip {\n  display: block;\n}\n.cc-categories .cc-tooltip {\n  display: none;\n  position: absolute;\n  z-index: 3;\n  width: 190px;\n  bottom: 46px;\n  padding: 8px;\n  border: thin solid lightgrey;\n  box-shadow: 1px 1px 4px rgba(150, 150, 150, 0.7);\n}\n.cc-categories .cc-tooltip:after {\n  content: \"\";\n  width: 10px;\n  height: 10px;\n  transform: rotate(45deg);\n  position: absolute;\n  bottom: -7px;\n  left: 10px;\n  box-shadow: 2px 1px 1px rgba(200, 200, 200, 0.5);\n}\n.cc-categories .cc-tooltip p {\n  margin: 0;\n}\n\n@media print {\n  .cc-window,\n.cc-revoke {\n    display: none;\n  }\n}\n@media screen and (max-width: 900px) {\n  .cc-btn {\n    white-space: normal;\n  }\n}\n/* dimensions for 'iPhone6 Plus' and lower */\n@media screen and (max-width: 414px) and (orientation: portrait), screen and (max-width: 736px) and (orientation: landscape) {\n  .cc-window.cc-top {\n    top: 0;\n  }\n  .cc-window.cc-bottom {\n    bottom: 0;\n  }\n  .cc-window.cc-banner, .cc-window.cc-floating, .cc-window.cc-right, .cc-window.cc-left {\n    left: 0;\n    right: 0;\n  }\n  .cc-window.cc-banner {\n    flex-direction: column;\n    align-items: unset;\n  }\n  .cc-window.cc-banner .cc-compliance {\n    flex: 1 1 auto;\n  }\n  .cc-window.cc-banner .cc-message {\n    margin-right: 0;\n    margin-bottom: 1em;\n  }\n  .cc-window.cc-floating {\n    max-width: none;\n  }\n  .cc-window.cc-type-categories {\n    flex-direction: column;\n  }\n  .cc-window .cc-message {\n    margin-bottom: 1em;\n  }\n  .cc-window .cc-categories {\n    flex-direction: column;\n    width: 100%;\n    margin-right: 8px;\n  }\n  .cc-window .cc-category {\n    margin: 4px 0;\n  }\n  .cc-window .cc-category .cc-btn:not(.cc-info) {\n    width: calc(100% - 16px);\n    min-width: 140px;\n  }\n}\n@media screen and (max-width: 854px) {\n  .cc-window.cc-type-categories .cc-btn.cc-save {\n    margin: 8px 0;\n  }\n}\n@media screen and (max-width: 790px) {\n  .cc-window.cc-type-categories .cc-categories {\n    display: flex;\n    flex-direction: column;\n  }\n\n  .cc-categories .cc-category {\n    margin: 4px 0;\n  }\n\n  .form {\n    width: 100%;\n    max-width: 350px;\n  }\n\n  .cc-btn:not(.cc-info):not(.cc-save) {\n    width: calc(100% - 16px);\n  }\n}\n/* Classic */\n.cc-floating.cc-theme-classic {\n  padding: 1.2em;\n  border-radius: 5px;\n}\n\n.cc-floating.cc-type-info.cc-theme-classic .cc-compliance {\n  text-align: center;\n  display: inline;\n  flex: none;\n}\n\n.cc-theme-classic {\n  overflow: visible;\n  justify-content: space-between;\n}\n.cc-theme-classic .cc-btn {\n  position: relative;\n  border-radius: 5px;\n  outline: none;\n}\n.cc-theme-classic .cc-btn:last-child {\n  min-width: 140px;\n}\n.cc-theme-classic .cc-category .cc-btn {\n  border-radius: 5px 0 0 5px;\n  padding-right: 2px;\n  padding-left: 28px;\n  font-weight: normal;\n  border-right: none;\n  box-sizing: border-box;\n}\n.cc-theme-classic .cc-category .cc-btn input[type=checkbox] {\n  position: absolute;\n  left: 0;\n  top: -1px;\n  width: 100%;\n  height: 26px;\n  opacity: 0;\n  cursor: pointer;\n  z-index: 999;\n}\n.cc-theme-classic .cc-category .cc-btn input[type=checkbox] + .cc-btn-checkbox {\n  display: block;\n  font-size: 2rem;\n  