\n  top: 0;\n  width: 100%;\n  height: 100%;\n  opacity: 0;\n  cursor: pointer;\n  z-index: 9;\n}\n\n.switch-toggle .cc-btn {\n  width: auto;\n  border: 0;\n  padding: 0;\n}\n\ninput[type=checkbox]:disabled + .switch-holder {\n  cursor: default;\n}\n\ninput[type=checkbox]:disabled ~ .dot {\n  opacity: 0.6;\n  pointer-events: none;\n}\n\n#spinner:not([hidden]) {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n#spinner::after {\n  content: \"\";\n  width: 40px;\n  height: 40px;\n  border: 2px solid #f3f3f3;\n  border-top: 3px solid #1c79f2;\n  border-radius: 100%;\n  will-change: transform;\n  animation: spin 1s infinite linear;\n}\n\n@keyframes spin {\n  from {\n    transform: rotate(0deg);\n  }\n  to {\n    transform: rotate(360deg);\n  }\n}\nbody,\nhtml {\n  margin: 0;\n  padding: 0;\n  font-family: \"Inter\", sans-serif;\n  font-size: 14px;\n  height: 100%;\n}\n\nbody.example3 {\n  height: auto;\n  z-index: 0;\n  /* background: #FFFFFF; */\n}\n\npre {\n  border-radius: 5px;\n  font-size: 12px;\n  line-height: 16px;\n  text-align: left;\n  background: black;\n  padding: 20px;\n  max-height: 300px;\n  width: 60%;\n  margin: 0 20% 30px 20%;\n  overflow: auto;\n}\n\npre .alert {\n  color: #d00;\n}\n\np a {\n  text-decoration: none;\n  border-bottom: 1px solid rgba(255, 255, 255, 0.3);\n}\n\np {\n  line-height: 1.4em;\n}\n\nem {\n  opacity: 0.7;\n  font-style: normal;\n}\n\nh1 span {\n  font-weight: lighter;\n  opacity: 0.6;\n}\n\nh3 {\n  opacity: 0.6;\n  font-weight: normal;\n}\n\n/**************** Page switcher ****************/\n.page-switcher {\n  background: rgba(0, 0, 0, 0.2);\n  color: rgba(255, 255, 255, 0.5);\n  text-decoration: none;\n  transition: all 0.25s;\n  position: fixed;\n  top: 50%;\n  height: 100px;\n  width: 100px;\n  margin-top: -50px;\n  font-size: 40px;\n  display: flex;\n  align-content: center;\n  align-items: center;\n  justify-content: center;\n