nline-block;\n  vertical-align: middle;\n  padding: 3px 5px;\n}\n\n/******************** SelectBox ********************/\n.selectbox {\n  text-align: center;\n  font-size: inherit;\n  max-width: 20em;\n  padding: 0.2em 0.4em;\n}\n\n/******************** Layout ********************/\n.center-outer {\n  display: table;\n}\n\n.center-inner {\n  display: table-cell;\n  vertical-align: middle;\n  text-align: center;\n}\n\n.fit {\n  width: 100%;\n  height: 100%;\n}\n\n/******************** Code ********************/\n#options {\n  display: none;\n}\n\n/******************** Responsiveness ********************/\n@media (min-width: 820px) and (max-width: 1000px) {\n  .example-window {\n    margin: 0 100px;\n  }\n}\n@media (max-width: 819px) {\n  body {\n    padding: 20px;\n  }\n\n  .example-window {\n    margin: 0;\n  }\n\n  .page-switcher {\n    position: inherit;\n    display: inline-block;\n    padding: 10px 10px;\n    width: 30px;\n    height: 30px;\n    font-size: 20px;\n    border-radius: 50%;\n    margin: 20px 0;\n  }\n}\n/* ========================= */\n.tab {\n  overflow: hidden;\n  border: 1px solid #ccc;\n  background-color: #f1f1f1;\n}\n\n.tab button {\n  background-color: inherit;\n  float: left;\n  border: none;\n  outline: none;\n  cursor: pointer;\n  padding: 14px 16px;\n  transition: 0.3s;\n}\n\n.tab button:hover {\n  background-color: #ddd;\n}\n\n.tab button.active {\n  background-color: #ccc;\n}\n\n.tabcontent {\n  display: none;\n  padding: 6px 12px;\n  border: 1px solid #ccc;\n  border-top: none;\n}\n\n.cc-type-info {\n  background-color: #052d61;\n}\n\n.CMPWrapper .bannerWrapper {\n  color: #ffffff;\n  font-weight: 500;\n  padding: 2em 1.8em;\n  position: relative;\n  line-height: 18px;\n}\n.CMPWrapper .bannerWrapper__description {\n  color: #a3b0c2;\n}\n.CMPWrapper .bannerWrapper__description button,\n.CMPWrapper .bannerWrapper__description a {\n  position: relative;\n  color: #ffffff !important;\n  margin-right: 1rem;\n}\n.CMPWrapper .bannerWrapper__description button::after,\n.CMPWrapper .bannerWrapper__description a::after {\n  content: \"\";\n  position: absolute;\n  bottom: 0px;\n  left: 0;\n  top: 100%;\n  background-color: #fff;\n  height: 2px;\n  width: 100%;\n}\n.CMPWrapper .bannerWrapper__controls button,\n.CMPWrapper .bannerWrapper__controls a {\n  border: 1px solid rgba(255, 255, 255, 0.2);\n  box-sizing: border-box;\n  background-color: #1d79f2;\n  border-color: #1d79f2;\n}\n.cmp-categories {\n  margin: auto;\n  height: 100vh;\n}\n.cmp-overlay {\n  display: flex;\n  visibility: visible;\n  position: fixed;\n  z-index: -1;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  align-items: center;\n  justify-content: center;\n  background-color: rgba(102, 124, 153, 0.4);\n  opacity: 1;\n  backface-visibility: hidden;\n  transition: opacity 0.1s cubic-bezier(0, 0.78, 0.03, 0.93) 0s, z-index 0s 0.1s, visibility 0s 0.1s;\n  will-change: visibility, z-index, opacity;\n}", "",{"version":3,"sources":["webpack://./src/styles/main.scss","webpack://./src/styles/animation.scss","webpack://./src/styles/base.scss","webpack://./src/styles/layout.scss","webpack://./src/styles/media.scss","webpack://./src/styles/themes/classic.scss","webpack://./src/styles/themes/edgeless.scss","webpack://./node_modules/tailwindcss/base.css","webpack://./node_modules/tailwindcss/components.css","webpack://./node_modules/tailwindcss/utilities.css","webpack://./src/styles/styles.scss","webpack://./src/styles/testStyles.scss"],"names":[],"mappings":"AAAA,gBAAgB;ACAhB;EACE,UAAA;ADIF;;ACKA;EAEE,UAAA;EAEA,mBAAA;ADJF;;ACOA;EAEE,UAAA;EACA,oBAAA;ADLF;;ACSA,0CAAA;AAwBA;EACE,8EAAA;EACA,aAAA;EACA,gBAAA;EAEA,4BAAA;EACA,iCAAA;EACA,8BAAA;EACA,6BAAA;EACA,4BAAA;EACA,yBAAA;AD9BF;;AE5BA,qBAAA;AACA;;EAEE,eAAA;EAGA,gBAAA;EACA,sBAAA;EAAwB,4CAAA;EACxB,gCAAA;EACA,eAAA;EAAiB,oFAAA;EACjB,kBAAA;EAEA,aAAA;EACA,iBAAA;EAEA,4FAAA;EACA,aAAA;AF6BF;;AE1BA;EACE,aAAA;AF6BF;;AE1BA;EACE,gBAAA;AF6BF;;AE1BA,gDAAA;AACA;EACE,YAAA;EACA,eAAA;EAAiB,wCAAA;EACjB,sBAAA;AF8BF;;AE5BA