;EACA,kBAAA;AX8fR;AW3fQ;;EACE,WAAA;EACA,kBAAA;EACA,WAAA;EACA,OAAA;EACA,SAAA;EACA,sBAAA;EACA,WAAA;EACA,WAAA;AX8fV;AWxfM;;EAEE,0CAAA;EACA,sBAAA;EAKA,yBAAA;EACA,qBAAA;AXsfR;AW1eA;EACE,YAAA;EACA,aAAA;AX4eF;AWreA;EACE,aAAA;EACA,mBAAA;EACA,eAAA;EACA,WAAA;EACA,MAAA;EACA,QAAA;EACA,SAAA;EACA,OAAA;EACA,mBAAA;EACA,uBAAA;EACA,0CAAA;EAEA,UAAA;EACA,2BAAA;EACA,kGAAA;EAEA,yCAAA;AXqeF","sourcesContent":["@import \"./tailwind.css\";\n@import \"./animation\";\n@import \"./base\";\n@import \"./layout\";\n@import \"./media\";\n@import \"./themes/all\";\n@import \"./styles\";\n@import \"./testStyles\";\n",".cc-window {\n  opacity: 1;\n\n  // -webkit-transition: opacity 1s ease;\n  // -moz-transition: opacity 1s ease;\n  // -ms-transition: opacity 1s ease;\n  // -o-transition: opacity 1s ease;\n  // transition: opacity 1s ease;\n}\n\n.cc-window {\n  // transition: opacity .3s ease, transform .3s ease;\n  opacity: 1;\n  // transform: scale(1);\n  pointer-events: all;\n}\n\n.cc-window.cc-invisible {\n  // transform: scale(1.03);\n  opacity: 0;\n  pointer-events: none;\n  // display: none !important;\n}\n\n/* only animate ifhas class 'cc-animate' */\n.cc-animate.cc-revoke {\n  // -webkit-transition: transform 1s ease;\n  // -moz-transition: transform 1s ease;\n  // -ms-transition: transform 1s ease;\n  // -o-transition: transform 1s ease;\n  // transition: transform 1s ease;\n}\n.cc-animate.cc-revoke.cc-top {\n  // transform: translateY(-2em);\n}\n.cc-animate.cc-revoke.cc-bottom {\n  // transform: translateY(2em);\n}\n.cc-animate.cc-revoke.cc-active.cc-top {\n  // transform: translateY(0);\n}\n.cc-animate.cc-revoke.cc-active.cc-bottom {\n  // transform: translateY(0);\n}\n.cc-revoke:hover {\n  // transform: translateY(0);\n}\n\n.cc-grower {\n  /* Initially we don't want any height, and we want the contents to be hidden */\n  max-height: 0;\n  overflow: hidden;\n\n  /* Set our transitions up. */\n  -webkit-transition: max-height 1s;\n  -moz-transition: max-height 1s;\n  -ms-transition: max-height 1s;\n  -o-transition: max-height 1s;\n  transition: max-height 1s;\n}\n","/* the popup window */\n.cc-window,\n.cc-revoke {\n  position: fixed;\n  // bottom: 1em;\n\n  overflow: hidden;\n  box-sizing: border-box; /* exclude padding when dealing with width */\n  font-family:  'Inter', sans-serif;\n  font-size: 14px; /* by setting the base font here, we can size the rest of the popup using CSS `em` */\n  line-height: 1.5em;\n\n  display: flex;\n  flex-wrap: nowrap;\n\n  /* the following are random unjustified styles - just because - should probably be removed */\n  z-index: 9998;\n}\n\n.cc-revoke {\n  z-index: 9999;\n}\n\n.cc-window.cc-static {\n  position: static;\n}\n\n/* 2 basic types of window - floating / banner */\n.cc-window.cc-floating {\n  padding: 2em;\n  max-width: 24em; /* 1em == 16px therefore 24em == 384px */\n  flex-direction: column;\n}\n.cc-window.cc-banner {\n  // padding: 2em 1.8em;\n  width: 100%;\n  flex-direction: row;\n}\n\n.cc-revoke {\n  padding: 0.5em;\n}\n.cc-revoke:hover {\n  text-decoration: underline;\n}\n\n.cc-header {\n  font-size: 18px;\n  font-weight: bold;\n}\n\n/* clickable things */\n.cc-btn,\n.cc-link,\n.cc-close,\n.cc-revoke {\n  cursor: pointer;\n}\n\n.cc-link {\n  opacity: 0.8;\n  display: inline-block;\n  padding: 0.2em;\n  text-decoration: underline;\n}\n.cc-link:hover {\n  opacity: 1;\n}\n.cc-link:active,\n.cc-link:visited {\n  color: initial;\n}\n\n.cc-btn {\n  display: block;\n  // padding: 0.4em 0.8em;\n  // font-size: 0.9em;\n  // font-weight: bold;\n  // border-width: 2px;\n  // border-style: solid;\n  text-align: center;\n  white-space: nowrap;\n}\n\n.cc-highlight .cc-btn:first-child {\n  background-color: transparent;\n  border-color: transparent;\n}\n\n.cc-highlight .cc-btn:first-child:hover,\n.cc-highlight .cc-btn:first-child:focus {\n  background-color: transparent;\n  text-decoration: underline;\n}\n\n.cc-close {\n  display: block;\n  // position: absolute;\n  // top: 0;\n  // left: 50%;\n  // transform: translateX(-50%);\n  font-size: 1.6em;\n  opacity: 0.9;\n  width: 100%;\n  height: 100%;\n\n  /* seeing as this contains text and not an image, the element taller than it is wide (because it is text) */\n  /*  - we want it to be a square, because it's acting as an icon */\n  /*  - setting the line height normalizes the height */\n  line-height: 0.75;\n}\n.cc-close:hover,\n.cc-close:focus {\n  opacity: 1;\n}\n","/* This file should contain styles that modifies the popup layout. */\n/* By layout, we mean the physical position of the elements on the popup window, and the margin / padding around those elements. */\n\n.cc-revoke.cc-top {\n  // top: 0;\n  // left: 3em;\n  // border-bottom-left-radius: 0.5em;\n  // border-bottom-right-radius: 0.5em;\n}\n.cc-revoke.cc-bottom {\n  // bottom: 0;\n  // left: 3em;\n  // border-top-left-radius: 0.5em;\n  // border-top-right-radius: 0.5em;\n}\n.cc-revoke.cc-left {\n  // left: 3em;\n  // right: unset;\n}\n.cc-revoke.cc-right {\n  // right: 3em;\n  // left: unset;\n}\n\n/**************************************** FLOATING ****************************************/\n\n/* these classes position the floating element */\n.cc-top {\n  top: 1em;\n}\n.cc-left {\n  left: 1em;\n}\n.cc-right {\n  right: 1em;\n}\n.cc-bottom {\n  bottom: 1em;\n}\n\n/* links that are direct decendants should be displayed as block */\n.cc-floating > .cc-link {\n  margin-bottom: 1em;\n}\n\n.cc-floating .cc-message {\n  display: block;\n  margin-bottom: 1em;\n}\n\n.cc-window.cc-floating .cc-compliance {\n  flex: 1 0 auto;\n}\n\n/**************************************** BANNER ****************************************/\n\n.cc-window.cc-banner {\n  align-items: center;\n}\n\n.cc-banner.cc-top {\n  left: 0;\n  right: 0;\n  top: 0;\n}\n.cc-banner.cc-bottom {\n  left: 0;\n  right: 0;\n  bottom: 0;\n}\n\n.cc-banner .cc-message {\n  display: block;\n  flex: 1 1 auto;\n  max-width: 100%;\n  margin-right: 1em;\n}\n\n/* COMPLIANCE BOX */\n\n.cc-compliance {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n}\n.cc-floating .cc-compliance > .cc-btn {\n  flex: 1;\n}\n\n.cc-btn + .cc-btn {\n  margin-left: 0.5em;\n}\n\n/* Categories Layout */\n.cc-window.cc-type-categories {\n  display: inline-flex;\n  flex-direction: column;\n  overflow: visible;\n  .form {\n    // text-align: right;\n  }\n  .cc-btn {\n    margin: 0;\n    &.cc-save {\n      margin: 0;\n      display: inline-block;\n    }\n  }\n}\n.cc-categories {\n  display: flex;\n  flex-direction: column;\n  .cc-category {\n    display: flex;\n    max-width: 100%;\n    // margin   : 0 2px;\n    position: relative;\n  }\n  .cc-btn {\n    border-right: none;\n    outline: none;\n    text-transform: capitalize;\n    // input[type=checkbox]{\n    //   float: left;\n    //   height: 26px;\n    //   width: calc( 100% - 22px );\n    //   display: block;\n    //   position: absolute;\n    //   top: 0;\n    //   left: 2px;\n    //   cursor: pointer;\n    // }\n    &:not(.cc-info):not(.cc-save) {\n      // padding-left: 26px;\n    }\n  }\n  .cc-info {\n    border-left: none;\n    border-right: 2px solid lightgrey;\n    padding: 4px;\n    font-variant: all-small-caps;\n    &:focus + .cc-tooltip {\n      display: block;\n    }\n  }\n  .cc-tooltip {\n    display: none;\n    position: absolute;\n    z-index: 3;\n    width: 190px;\n    bottom: 46px;\n    padding: 8px;\n    border: thin solid lightgrey;\n    box-shadow: 1px 1px 4px rgba(150, 150, 150, 0.7);\n    &:after {\n      content: \"\";\n      width: 10px;\n      height: 10px;\n      transform: rotate(45deg);\n      position: absolute;\n      bottom: -7px;\n      left: 10px;\n      box-shadow: 2px 1px 1px rgba(200, 200, 200, 0.5);\n    }\n    p {\n      margin: 0;\n    }\n  }\n}\n","@media print {\n  .cc-window,\n  .cc-revoke {\n    display: none;\n  }\n}\n\n@media screen and (max-width: 900px) {\n  .cc-btn {\n    white-space: normal;\n  }\n}\n\n/* dimensions for 'iPhone6 Plus' and lower */\n@media screen and (max-width: 414px) and (orientation: portrait),\n  screen and (max-width: 736px) and (orientation: landscape) {\n  .cc-window {\n    &.cc-top {\n      top: 0;\n    }\n    &.cc-bottom {\n      bottom: 0;\n    }\n    &.cc-banner,\n    &.cc-floating,\n    &.cc-right,\n    &.cc-left {\n      left: 0;\n      right: 0;\n    }\n    \n    &.cc-banner {\n      flex-direction: column;\n      align-items: unset;\n      .cc-compliance {\n        flex: 1 1 auto;\n      }\n      .cc-message {\n        margin-right: 0;\n        margin-bottom: 1em;\n      }\n    }\n    &.cc-floating {\n      max-width: none;\n    }\n    &.cc-type-categories {\n      flex-direction: column;\n    }\n    .cc-message {\n      margin-bottom: 1em;\n    }\n    .cc-categories{\n      flex-direction: column;\n      width: 100%;\n      margin-right: 8px;\n    }\n    .cc-category {\n      margin: 4px 0;\n      .cc-btn:not(.cc-info){\n        width: calc( 100% - 16px);\n        min-width: 140px;\n      }\n    }\n  } \n}\n@media screen and (max-width: 854px) {\n  .cc-window.cc-type-categories .cc-btn.cc-save {\n    margin: 8px 0;\n  }\n}\n\n@media screen and (max-width: 790px) {\n  .cc-window.cc-type-categories .cc-categories{\n    display: flex;\n    flex-direction: column;\n  }\n  .cc-categories .cc-category{\n    margin: 4px 0;\n  }\n  .form {\n    width    : 100%;\n    max-width: 350px;\n  }\n  .cc-btn:not(.cc-info):not(.cc-save){\n    width: calc( 100% - 16px);\n  }\n}\n","/* Classic */\n\n.cc-floating.cc-theme-classic {\n  padding: 1.2em;\n  border-radius: 5px;\n}\n\n.cc-floating.cc-type-info.cc-theme-classic {\n  .cc-compliance {\n    text-align: center;\n    display: inline;\n    flex: none;\n  }\n}\n\n.cc-theme-classic {\n  overflow: visible;\n  justify-content: space-between;\n  .cc-btn {\n    position: relative;\n    border-radius: 5px;\n    outline: none;\n    &:last-child {\n      min-width: 140px;\n    }\n  }\n  .cc-category {\n    .cc-btn {\n      border-radius: 5px 0 0 5px;\n      padding-right: 2px;\n      padding-left: 28px;\n      font-weight: normal;\n      border-right: none;\n      box-sizing: border-box;\n      input[type=\"checkbox\"] {\n        position: absolute;\n        left: 0;\n        top: -1px;\n        width: 100%;\n        height: 26px;\n        opacity: 0;\n        cursor: pointer;\n        z-index: 999;\n        & + .cc-btn-checkbox {\n          display: block;\n          font-size: 2rem;\n          position: absolute;\n          top: 2px;\n          left: 6px;\n          z-index: 0;\n          outline: none;\n          &:before {\n            content: \"\\1F5F5\";\n          }\n        }\n      }\n      input[type=\"checkbox\"]:checked + .cc-btn-checkbox {\n        &:after {\n          content: \"\\2713\";\n          position: absolute;\n          top: -4px;\n          left: 0;\n          font-size: 2.3rem;\n          text-shadow: 0 1px 3px rgba(150, 150, 150, 0.5);\n        }\n      }\n      &.cc-info {\n        margin: 0;\n        padding: 0 4px;\n        border-radius: 0 5px 5px 0;\n      }\n      &:last-child {\n        min-width: 0;\n      }\n    }\n    .cc-tooltip {\n      border-radius: 5px;\n      &:after {\n        border-bottom: thin solid lightgrey;\n        border-right: thin solid lightgrey;\n      }\n    }\n  }\n}\n\n.cc-floating.cc-type-info.cc-theme-classic .cc-btn {\n  display: inline-block;\n}\n","/* Edgeless */\n\n.cc-theme-edgeless.cc-window {\n  padding: 0;\n}\n\n.cc-floating.cc-theme-edgeless .cc-message {\n  margin: 2em;\n  margin-bottom: 1.5em;\n}\n\n.cc-banner.cc-theme-edgeless .cc-btn {\n  margin: 0;\n  padding: 0.8em 1.8em;\n  height: 100%;\n}\n\n.cc-banner.cc-theme-edgeless .cc-message {\n  margin-left: 1em;\n}\n\n.cc-floating.cc-theme-edgeless .cc-btn + .cc-btn {\n  margin-left: 0;\n}\n\n.cc-window.cc-theme-edgeless.cc-type-categories{\n  .cc-categories {\n    .cc-btn{\n      padding: 0.4em 0.8em;\n      padding-left: 26px;\n      &.cc-info{\n        padding: 0.4em 4px;\n      }\n    }\n    .cc-tooltip{\n      border: none;\n    }\n  }\n}\n","@tailwind base;\n","@tailwind components;\n","@tailwind utilities;\n","// @import \"./main.scss\";\n\n/* EXAMPLE 3  */\n/* Checkbox style */\n// @tailwind base;\n// @tailwind utilities;\n// @tailwind components;\n@use \"tailwindcss/base\";\n@use \"tailwindcss/components\";\n@use \"tailwindcss/utilities\";\n\n.contentWrapper {\n  // max-height: 100%;\n  // max-height: calc(100% - 210px);\n  // height: calc(100% - 120px);\n\n  height: 500px;\n  @media only screen and (min-width: 1536px) {\n    // max-height: calc(100% - 130px);\n  }\n}\n.cookieModal {\n  // max-height: 80vh;\n  height: 100%;\n  // max-height: calc(100vh - 330px);\n  @media only screen and (min-width: 480px) {\n    // max-height: calc(100% - 330px);\n  }\n  @media only screen and (min-width: 1536px) {\n    // max-height: calc(100% - 330px);\n  }\n}\n\ninput[type=\"checkbox\"]:checked ~ .dot {\n  transform: translateX(12px);\n  background-color: #1c79f2;\n}\n\n.switch-toggle input  {\n  position: absolute;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  opacity: 0;\n  cursor: pointer;\n  z-index: 9;\n}\n\n.switch-toggle .cc-btn {\n  width: auto;\n  border: 0;\n  padding: 0;\n}\n\n// .categoryChecked{\n//   transform: translateX(12px);\n//   background-color: #1c79f2;\n// }\n\ninput[type=\"checkbox\"]:disabled + .switch-holder {\n  cursor: default;\n}\n\ninput[type=\"checkbox\"]:disabled ~ .dot {\n  opacity: 0.6;\n  pointer-events: none;\n}\n\n// .checkbox:hover div {\n//   @apply border-gray-500;\n// }\n\n// .checkbox-wrapper input:checked + .icon {\n//   background-color: red;\n//   border-color: blue;\n\n//   /* background-color: blue; */\n//   /* */\n//   /*\n//   @a*/\n// }\n// .checkbox-wrapper input:checked ~ .dot {\n//   transform: translateX(0.75rem);\n//   background-color: red;\n// }\n\n//! spinner on loading\n#spinner:not([hidden]) {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n#spinner::after {\n  content: \"\";\n  width: 40px;\n  height: 40px;\n  border: 2px solid #f3f3f3;\n  border-top: 3px solid #1c79f2;\n  border-radius: 100%;\n  will-change: transform;\n  animation: spin 1s infinite linear;\n}\n\n@keyframes spin {\n  from {\n    transform: rotate(0deg);\n  }\n  to {\n    transform: rotate(360deg);\n  }\n}\n","@import url(\"https://fonts.googleapis.com/css2?family=Inter:wght@100;300;500;600;700;800;900&display=swap\");\n\nbody,\nhtml {\n  margin: 0;\n  padding: 0;\n  font-family: \"Inter\", sans-serif;\n  font-size: 14px;\n  height: 100%;\n  // background: #3f4f79;\n  // background: radial-gradient(ellipse at center, #3f4f79 0%, #1d1d2b 100%);\n}\n\nbody.example3 {\n  // background: #7f5f49;\n  height: auto;\n  // background: rgba(102, 124, 153, 0.4);\n  // background: #667c99;\n  // opacity: 0.4;\n  z-index: 0;\n  /* background: #FFFFFF; */\n  // background: radial-gradient(ellipse at center, #5f7f49 0%, #1d1d2b 100%);\n}\n\npre {\n  border-radius: 5px;\n  font-size: 12px;\n  line-height: 16px;\n  text-align: left;\n  background: black;\n  padding: 20px;\n  max-height: 300px;\n  width: 60%;\n  margin: 0 20% 30px 20%;\n  overflow: auto;\n}\n\npre .alert {\n  color: #d00;\n}\n\np a {\n  text-decoration: none;\n  border-bottom: 1px solid rgba(255, 255, 255, 0.3);\n}\n\np {\n  line-height: 1.4em;\n}\n\nem {\n  opacity: 0.7;\n  font-style: normal;\n}\n\nh1 span {\n  font-weight: lighter;\n  opacity: 0.6;\n}\n\nh3 {\n  opacity: 0.6;\n  font-weight: normal;\n}\n\n/**************** Page switcher ****************/\n\n.page-switcher {\n  background: rgba(0, 0, 0, 0.2);\n  color: rgba(255, 255, 255, 0.5);\n  text-decoration: none;\n  transition: all 0.25s;\n  position: fixed;\n  top: 50%;\n  height: 100px;\n  width: 100px;\n  margin-top: -50px;\n  font-size: 40px;\n  display: flex;\n  align-content: center;\n  align-items: center;\n  justify-content: center;\n}\n\n.page-switcher:hover {\n  background: rgba(0, 0, 0, 0.4);\n}\n\n.page-switcher-prev {\n  left: 0;\n}\n\n.page-switcher-prev:hover {\n  left: -10px;\n}\n\n.page-switcher-next {\n  right: 0;\n}\n\n.page-switcher-next:hover {\n  right: -10px;\n}\n\n/**************** JavaScript API examples ****************/\n\n#console {\n  height: 100px;\n  width: 100%;\n  overflow-x: hidden;\n  overflow-y: scroll;\n  margin: 0 0 30px 0;\n}\n\n/******************** Example window ********************/\n\n.example-window {\n  text-align: center;\n  color: #e9e9e9;\n  max-width: 40em;\n  display: inline-block;\n}\n\n.example-window h1 {\n  margin: 0 0 0.5em 0;\n}\n\n.example-window p {\n  margin: 0 0 1em 0;\n}\n\n.example-window button {\n  margin: 0;\n  padding: 0.5em;\n  font-size: 1em;\n}\n\n/******************** Forms ********************/\n\n.form-controls {\n  margin: 40px;\n}\n\n.form-controls label {\n  display: block;\n  text-align: center;\n  opacity: 0.5;\n  margin-bottom: 10px;\n}\n\n.btn-group {\n  margin-bottom: 10px;\n}\n\n.btn,\n.btn:visited {\n  display: inline-block;\n  background: #fff;\n  color: #000;\n  border: 0;\n  padding: 0.5em 1.4em;\n  text-decoration: none;\n  border-radius: 0.2em;\n  transition: all 0.25s;\n  margin: 0 0.25em;\n}\n\n.btn:hover {\n  background: #ddd;\n  color: #000;\n}\n\n/******************** Example selector ********************/\n\nul {\n  list-style: none;\n  padding: 0;\n}\n\n.example-selector > li {\n  display: inline-block;\n  height: 6em;\n  width: 6em;\n  color: rgba(0, 0, 0, 0.7);\n  background: rgba(255, 255, 255, 0.7);\n  text-align: center;\n  cursor: pointer;\n  margin: 0.5em;\n  padding: 0.5em;\n  overflow: hidden;\n  transition: all 0.25s;\n  border-radius: 5px;\n}\n\n.example-selector > li:hover {\n  color: rgba(0, 0, 0, 1);\n  background: rgba(255, 255, 255, 0.9);\n}\n\n.example-selector > li:before {\n  content: \"\";\n  display: inline-block;\n  vertical-align: middle;\n  height: 100%;\n}\n\n.example-selector > li > span {\n  display: inline-block;\n  vertical-align: middle;\n  padding: 3px 5px;\n}\n\n/******************** SelectBox ********************/\n\n.selectbox {\n  text-align: center;\n  font-size: inherit;\n  max-width: 20em;\n  padding: 0.2em 0.4em;\n}\n\n/******************** Layout ********************/\n\n.center-outer {\n  display: table;\n}\n\n.center-inner {\n  display: table-cell;\n  vertical-align: middle;\n  text-align: center;\n}\n\n.fit {\n  width: 100%;\n  height: 100%;\n}\n\n/******************** Code ********************/\n\n#options {\n  display: none;\n}\n\n/******************** Responsiveness ********************/\n\n@media (min-width: 820px) and (max-width: 1000px) {\n  .example-window {\n    margin: 0 100px;\n  }\n}\n\n@media (max-width: 819px) {\n  body {\n    padding: 20px;\n  }\n\n  .example-window {\n    margin: 0;\n  }\n\n  .page-switcher {\n    position: inherit;\n    display: inline-block;\n    padding: 10px 10px;\n    width: 30px;\n    height: 30px;\n    font-size: 20px;\n    border-radius: 50%;\n    margin: 20px 0;\n  }\n}\n\n/* ========================= */\n//! Style the tab\n.tab {\n  overflow: hidden;\n  border: 1px solid #ccc;\n  background-color: #f1f1f1;\n}\n\n//! Style the buttons that are used to open the tab content\n.tab button {\n  background-color: inherit;\n  float: left;\n  border: none;\n  outline: none;\n  cursor: pointer;\n  padding: 14px 16px;\n  transition: 0.3s;\n}\n\n//! Change background color of buttons on hover\n.tab button:hover {\n  background-color: #ddd;\n}\n\n//! Create an active/current tablink class\n.tab button.active {\n  background-color: #ccc;\n}\n\n//! Style the tab content\n.tabcontent {\n  display: none;\n  padding: 6px 12px;\n  border: 1px solid #ccc;\n  border-top: none;\n}\n\n.cc-type-info {\n  background-color: #052d61;\n}\n\n//! small consent banner visible on page load\n.CMPWrapper {\n  // background-color: #052d61;\n  // position: absolute;\n  // top: 0;\n  // transform: translateY(100%);\n  // transition: all 500ms ease-in-out;\n  .bannerWrapper {\n    color: #ffffff;\n    font-weight: 500;\n    padding: 2em 1.8em;\n    position: relative;\n    // font-size: 14px;\n    line-height: 18px;\n\n    &__description {\n      color: #a3b0c2;\n\n      button,\n      a {\n        position: relative;\n        color: #ffffff !important;\n        margin-right: 1rem;\n\n        // text-decoration: underline;\n        &::after {\n          content: \"\";\n          position: absolute;\n          bottom: 0px;\n          left: 0;\n          top: 100%;\n          background-color: #fff;\n          height: 2px;\n          width: 100%;\n        }\n      }\n    }\n\n    &__controls {\n      button,\n      a {\n        border: 1px solid rgba(255, 255, 255, 0.2);\n        box-sizing: border-box;\n        // &:nth-child(1) {\n        //   margin-right: 2rem;\n        // }\n        // &:nth-child(2) {\n        background-color: #1d79f2;\n        border-color: #1d79f2;\n        // }\n      }\n    }\n  }\n\n  &.showModal {\n    // transform: translateY(0);\n  }\n}\n\n//!CATEGORIES BANNER STYLES\n.cmp-categories {\n  margin: auto;\n  height: 100vh;\n\n  & > * {\n    // margin: auto;\n  }\n}\n\n.cmp-overlay {\n  display: flex;\n  visibility: visible;\n  position: fixed;\n  z-index: -1;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  align-items: center;\n  justify-content: center;\n  background-color: hsla(214, 20%, 50%, 40%);\n  // background-color: #667c99;\n  opacity: 1;\n  backface-visibility: hidden;\n  transition: opacity 0.1s cubic-bezier(0, 0.78, 0.03, 0.93) 0s, z-index 0s 0.1s,\n    visibility 0s 0.1s;\n  will-change: visibility, z-index, opacity;\n}"],"sourceRoot":""}]);
